import { defineComponent } from "vue";
const cards = [
    { text: "Ace", value: 1 },
    { text: "2", value: 2 },
    { text: "3", value: 3 },
    { text: "4", value: 4 },
    { text: "5", value: 5 },
    { text: "6", value: 6 },
    { text: "7", value: 7 },
    { text: "8", value: 8 },
    { text: "9", value: 9 },
    { text: "10", value: 10 },
    { text: "Jack", value: 11 },
    { text: "Queen", value: 12 },
    { text: "King", value: 13 },
];
const faces = ["Clubs", "Diamonds", "Hearts", "Spades"];
export default defineComponent({
    props: {
        id: {
            type: Number,
            required: true,
        },
        value: {
            type: Number,
            required: true,
        },
        colorScheme: {
            type: String,
            required: true,
        },
    },
    emits: {
        select(selected) {
            return selected >= 1 && selected <= 13;
        },
    },
    data() {
        return {
            cards,
            selected: this.value,
        };
    },
    computed: {
        image() {
            const face = faces[Math.floor(Math.random() * 4)];
            return {
                src: `images/${this.colorScheme}/${this.selected}${face[0]}.svg`,
                alt: `${cards[this.selected - 1].text} of ${face}`,
                title: `${cards[this.selected - 1].text} of ${face}`,
            };
        },
    },
    watch: {
        selected(newValue) {
            this.$emit("select", newValue);
        },
        value(newValue) {
            this.selected = newValue;
        },
    },
});
