export const Add = {
    operate(a, b) {
        return a + b;
    },
    toString(a, b) {
        return `${a} + ${b}`;
    },
};
export const Subtract = {
    operate(a, b) {
        return a - b;
    },
    toString(a, b) {
        return `${a} - ${b}`;
    },
};
export const Multiply = {
    operate(a, b) {
        return a * b;
    },
    toString(a, b) {
        return `${a} * ${b}`;
    },
};
export const Divide = {
    operate(a, b) {
        return a / b;
    },
    toString(a, b) {
        return `${a} / ${b}`;
    },
};
export const Exponent = {
    operate(a, b) {
        return a ** b;
    },
    toString(a, b) {
        return `${a} ^ ${b}`;
    },
};
export const Log = {
    operate(a, b) {
        return Math.log(b) / Math.log(a);
    },
    toString(a, b) {
        return `log_${a}(${b})`;
    },
};
