import { defineComponent } from "vue";
import CardPicker from "./CardPicker.vue";
export default defineComponent({
    components: {
        CardPicker,
    },
    props: {
        hand: {
            type: Array,
            required: true,
        },
        colorScheme: {
            type: String,
            required: true,
        },
    },
    emits: {
        handUpdated({ id, value }) {
            return id >= 0 && id < 4 && value >= 1 && value <= 13;
        },
    },
    methods: {
        updateHand(id, value) {
            this.$emit("handUpdated", { id, value });
        },
    },
});
