import { defineComponent } from "vue";
export default defineComponent({
    props: {
        solutions: {
            type: Array,
            required: true,
        },
    },
    emits: {
        redraw: null,
    },
    data() {
        return {
            showSolutions: false,
        };
    },
    computed: {
        hasSolutions() {
            return this.solutions.length !== 0;
        },
    },
    watch: {
        solutions() {
            this.showSolutions = false;
        },
    },
    methods: {
        toggleSolutions() {
            this.showSolutions = !this.showSolutions;
        },
    },
});
